        "id": "NE002",
        "name": "abandoned tiki bar",
        "artist": "Static Breakfast",
        "date": "March 2020",
        "description": "Abandoned Tiki bar arrived in our mailbox along with a message from the Static Breakfast ensemble. It was a pocket dial I'd guess, muffled, and I could only make out one voice, but as best as I can make out here's what it said:\n\n\"Now that's strange.\n\nI could have sworn this was the place. It looks the same, but something changed. Something is, uhh, I'm not sure of the best way to say it. Wrong?  I'm not sure that's exactly right. Something is different, that's probably more like it.\n\nYeah, last time we came it was a blast. All the same vintage Hawaiian style decorations, funky drink glasses, and wacky drink specials. Yeah, totally, those little umbrellas and all that shit haha. Our waitress was hilarious. And it was pretty cheap! They played that Santo and Johnny song like 8 times. What's it called? Yeah, sleep walk. That's definitely it.\n\nYou see the bartender? Yeah that's him. Let's see if he knows what's up. Hey. Hey! Can I get a drink? Huh. Maybe he can't hear me...\n\nWanna bounce? Yeah, let's go up the street. I'm getting creeped out. Plus it's freezing in here.\n\nYou smell that? Nah, nothing. Don't worry about it. Yeah, sure. Oh, ok. I'll just catch you later. Totally, see you in a bit...\"\n\nCryptic messages and mysterious happenings are only some of the vibe in the ‘Abandoned Tiki Bar.' It's the place this music is of, and for, and the place few want to find themselves. But a few do. And it's to those unique individuals this tape is dedicated.\n\nGood luck and stay safe out there. ",
